import React, { Component } from 'react'
import './index.css'

export default class Weather extends Component {
  constructor(){
    super()
    this.state = {
      weather:{
        cond_code: ''
      },
      tips:'',
      iconImg:''
    }
  }
  componentWillMount(){
    fetch('https://free-api.heweather.net/s6/weather/now?location=shiyan&key=HE1903061702461886').then((res)=>{
      return res.json()
    }).then((data)=>{
      if(data.HeWeather6&&data.HeWeather6[0].status === 'ok'){
        let iconImg = `https://cdn.heweather.com/cond_icon/${data.HeWeather6[0].now.cond_code}.png`
        this.setState({
          weather: data.HeWeather6[0].now,
          tips:'我不在身边要好好照顾自己嘤嘤嘤QAQ',
          iconImg
        })
      }else{
        this.setState({
          weather: '不知道天气怎么样呢',
          tips:'我不在身边要好好照顾自己QAQ'
        })
      }
    })
  }
  render() {
    return (
      <div className='wrap'>
        <div className='weather'>
          {this.state.weather.cond_txt}
          <img alt='' src={this.state.iconImg} className='weatherIcon'/>
        </div>
        <div className='tips'>
          {this.state.tips}
        </div>
      </div>
    )
  }
}
