import React from 'react'
import './index.css'

class Clock extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            time: new Date().toLocaleTimeString(),
            date: new Date().toLocaleTimeString()
        }
    }
    componentDidMount(){
        fetch('http://api.833cc.cc/v1/ServerTime').then((res)=>{
            return res.json()
        }).then((data)=>{
            if(data.code === 200){
                let cha  = new Date().getTime() - data.data;

                if(Math.abs(cha)<= 30000){
                    
                    this.timer = setInterval(()=>{
                        this.setState({
                            time: new Date().toLocaleTimeString(),
                            date: new Date().toLocaleDateString(),
                            day: new Date().getDay()
                        })
                    },100)
                }else{
                    console.log(cha)
                    this.setState({
                        time: '小可爱的电脑的时间不准！！！'
                    })
                }
            }
        })
        
    }
    componentWillUnmount(){
        clearInterval(this.timer)
    }
    render(){
        return (
            <div className='timeBox'>
                <span className='date'>{this.state.date}</span>
                <span className='time'>{this.state.time}</span>
                <span className='day'>星期{this.state.day}</span>
            </div>
        )
    }
}

export default Clock